___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: grey;
}

header {
  width: 100%;
  background-color: black;
  color: white;
  text-align: center;
  padding: 10px;
  font-family: "Bitcount Prop Single", sans-serif;
  font-size: 4rem;
}

.game-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
  font-family: "Bitcount Prop Single", sans-serif;
  font-size: 1.5rem;
  text-align: center;
  color: white;
}

.button-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.dom-content {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2vw;
  height: 100%;
  width: 100%;
  min-height: 400px;
  max-height: 900px;
  min-width: 500px;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  background-color: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.board-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1 1 0;
  min-width: 250px;
  max-width: 600px;
}

.player-label {
  text-align: center;
  width: 100%;
  max-width: 500px;
  margin-bottom: 0.5em;
  font-family: "Bitcount Prop Single", sans-serif;
  font-size: 2rem;
  color: #222;
  letter-spacing: 1px;
}

.player-label.active-label {
  box-shadow:
    0 0 20px 6px #3b8bff,
    0 0 0 0px #333 inset;
  border-radius: 8px;
  background: #eaf4ff;
  transition:
    box-shadow 0.3s,
    background 0.3s;
}

.gameboard-left,
.gameboard-right {
  display: grid;
  grid-template-rows: repeat(10, 1fr);
  grid-template-columns: repeat(10, 1fr);
  width: 100%;
  min-height: 200px;
  max-height: 500px;
  min-width: 200px;
  max-width: 500px;
  margin: auto;
  aspect-ratio: 1 / 1;
  background-color: white;
  border: 2px solid black;
}

.axis-label {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Special Elite", cursive;
  font-size: 1.1em;
  background: #f5f5f5;
  color: #333;
  border: none;
  user-select: none;
}
.axis-x {
  border-bottom: 2px solid #bbb;
}
.axis-y {
  border-right: 2px solid #bbb;
}
.axis-corner {
  background: #f5f5f5;
  border: none;
}
.grid-cell {
  border: 1px solid #ccc;
  width: 100%;
  aspect-ratio: 1 / 1;
}

.ship-placed {
  background-color: black;
}

.ship-preview {
  border: 2px solid black;
  background-color: lightgrey;
}

.ship-sunk {
  position: relative;
}

.ship-sunk::after {
  content: "✖";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  font-size: 1.8em;
  font-weight: bold;
  pointer-events: none;
  text-shadow:
    0 0 6px #222,
    0 0 2px #ff483b;
}

.active-board {
  box-shadow:
    0 0 20px 6px #ff483b,
    0 0 0 0px #333 inset;
  z-index: 1;
  transition: box-shadow 0.3s;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}
.modal-content {
  background: #fff;
  padding: 2em;
  border-radius: 8px;
  text-align: center;
}
.modal p {
  margin-bottom: 1.5em;
}

/* general button style for all game buttons */
button,
.play-again-button,
.start-button,
.reset-button,
.confirm-button,
.mode-button,
.difficulty-button {
  padding: 0.3em 0.3em;
  font-size: 1rem;
  font-family: "Bitcount Prop Single", "Special Elite", monospace, sans-serif;
  border: 2px solid #1a2636;
  border-radius: 10px;
  background: linear-gradient(180deg, #3b4c5e 0%, #1a2636 100%);
  color: #eaf4ff;
  box-shadow:
    0 2px 8px rgba(26, 38, 54, 0.18),
    0 1.5px 0 #22334a inset;
  letter-spacing: 2px;
  cursor: pointer;
  transition:
    background 0.2s,
    box-shadow 0.2s,
    transform 0.1s;
  margin: 0 0.2em;
  outline: none;
}

button:hover,
.play-again-button:hover,
.start-button:hover,
.reset-button:hover,
.confirm-button:hover,
.mode-button:hover,
.difficulty-button:hover {
  background: linear-gradient(180deg, #22334a 0%, #3b4c5e 100%);
  box-shadow:
    0 4px 16px rgba(26, 38, 54, 0.28),
    0 2px 0 #22334a inset;
  transform: translateY(-2px) scale(1.04);
}

button:active,
.play-again-button:active,
.start-button:active,
.reset-button:active,
.confirm-button:active,
.mode-button:active,
.difficulty-button:active {
  background: linear-gradient(180deg, #16202c 0%, #22334a 100%);
  box-shadow:
    0 1px 4px rgba(26, 38, 54, 0.22),
    0 1px 0 #22334a inset;
  transform: translateY(1px) scale(0.98);
}

button:focus,
.play-again-button:focus,
.start-button:focus,
.reset-button:focus,
.confirm-button:focus,
.mode-button:focus,
.difficulty-button:focus {
  outline: 2px solid #6be0ff;
  outline-offset: 2px;
}
